  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  overflow-y: auto;
}

.jp-CommentsWidget h2 {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.jp-CommentsWidget input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.jp-CommentsWidget button {
  padding: 5px 10px;
  background-color: #0078d4;
  color: white;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.jp-CommentsWidget button:hover {
  background-color: #005a9e;
}

.jp-CommentsWidget ul {
  list-style-type: none;
  padding: 0;
}

.jp-CommentsWidget li {
  padding: 5px 0;
  border-bottom: 1px solid #eee;
